import styles from 'styled-components';

const AboutBlock = styles.div`
  font-weight: 500;
  font-size: 18px;
`;

const List = styles.div`
  margin-left: 15px;
  & li {
    font-size: 16px;
    font-weight: 500;
  }
`;

function About() {
    return (
        <div className='container'>
            <AboutBlock>
                Это приложение построено с помощью React JS, в качестве state managere используется
                Redux Toolkit и Redux Thunk для асинхронных операций, также используется TypeScript
                для типизации.Для стилей использовался препроцессор SASS, также использовались такие
                распространенные библиотеки как:
            </AboutBlock>
            <List>
                <li>axios,</li>
                <li>lodash.debounce,</li>
                <li>react-content-loader,</li>
                <li>react-paginate,</li>
                <li>react-router-dom,</li>
                <li>styled-components</li>
            </List>
        </div>
    );
}

export default About;
